
import sql from 'common/data/websql'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import Search from 'vux/src/components/search/index.vue'

import sortMenu from 'components/sortMenu.vue'
import headIcon from 'components/headIcon.vue'
import {plusready} from 'common/plus'

export default {
data(){
return {
showContent: true,
headList: [{
id: '0',
shortLabel: this.$t('组织')
}],
contList: [],
childrenList: [],
sql: '',
search: true,
seaList: [{
id: '-1',
shortLabel: this.$t('空')
}],
nowIndex: 0,
clickItem: null
}
},
components: {
Search,
sortMenu,
XImg,
headIcon,
Cell,
CellBox
},
created(){
this.sql = new sql();
this.init();
plusready(this.plusready)
},
mounted(){
},
watch: {
clickItem(val){
this.slqQuery(val.id).then(v => {
if (!v) return;
this.contList = v;
this.headClick(val);
})
}
},
methods: {
plusready(){
this.cw = plus.webview.currentWebview();
},
init(){
this.slqQuery('0').then(res => {
if (!res) return
this.headList = res;
this.clickItem = res[0];
})
},
refresh(done){
setTimeout(() => {
this.sql.clearInsert().then(res => {
if (res) {
this.init();
}
done();
}).catch(err => {
mui.toast(err)
done();
});
}, 1000)
},
onFocus(){
this.search = false;
},
onCancel(){
this.search = true;
this.seaList = [{
id: '-1',
shortLabel: this.$t('空')
}]
},
getResult(val){
if (val == '')return;
this.sql.select('user_info', '*', `nodeType = 0 and shortLabel like "%${val}%"`, [], rows => {
let items = [];
if (rows) {
for (let i = 0; i < rows.length; i++) {
items.push(rows.item(i))
}
this.seaList = items;
}
})
},
back(){
this.cw.close();
},
onSubmit(){
this.$refs.search.setBlur();
this.search = true;
},
slqQuery(id, type){
type = !type ? 'pid' : type
return new Promise((resovle, reject) => {
this.sql.select('user_info', '*', type + ' = ?', [id], rows => {
let items = [];
if (rows) {
for (let i = 0; i < rows.length; i++) {
items.push(rows.item(i))
}
}
if (items.length == 0) return resovle(false);
resovle(items);
})
})
},
openAlert(node){
if (node.nodeType == 0) { //点击人员操作
node.deptName = this.clickItem.shortLabel;
this.$store.commit('SET_SHOPMSG', node);
this.$router.push({name: 'more'});
return
}
this.clickItem = node;
},
naClick(val){
this.clickItem = val;
},
headClick(value){
let nowitem = this.headList.findIndex(v => {
return (v.id == value.id) ? v : false
})
if (nowitem == -1) {
this.headList.push({
id: value.id,
shortLabel: value.shortLabel
})
return
}
this.headList = this.headList.filter((item, x) => {
if (x <= nowitem) return item
})
}
}
}



