export default {
  props: ['list', 'props'],
  name: 'allSort',
  data () {
    return {}
  },
  methods: {
    click(x, val){
      if (x == (this.list.length - 1)) return
      this.$emit('na-click', val);
    }
  },
  computed: {
    newProp(){
      return this.props && this.props.label ? this.props.label : 'shortLabel'
    }
  }
}



